.featured-blogs-container {
  margin-top: 1.5rem;
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;

  .blog-card {
    background-color: rgb(247, 243, 239);
    border-radius: 5px;
    border: 2px;
    border-style: groove;
    border-color: rgb(0, 0, 0, 0.2);
    display: flex;
    width: 49%;

    .description {
      text-align: left;
      padding-left: 2rem;

      h2 {
        margin-top: 0.5rem;
        margin-bottom: 0.1rem;
      }

      h3 {
        margin-bottom: 0rem;
        color: rgb(66, 160, 102);
      }

      h4 {
        color: rgb(0, 119, 255);
      }

      span {
        color: rgb(0, 0, 0, 0.6);
        font-size: 15px;
      }

      p {
        max-width: 20rem;
        height: 4.5rem;
        padding-right: 1rem;
        overflow-y: hidden;
        overflow-x: hidden;
      }
    }

    .thumbnail {
      background-color: rgb(0, 0, 0, 0.6);
      width: 12rem;
      border-top-right-radius: 5px;
      border-bottom-right-radius: 5px;

      img {
        width: 100%;
        height: 100%;
        border-top-right-radius: 5px;
      border-bottom-right-radius: 5px;
      }
    }
  }
}

@media (max-width: 600px) {
  .featured-blogs-container {
    margin-top: 0px;
    flex-direction: column;

    .blog-card {
      margin-top: 1rem;
      width: 100%;
    }
    .thumbnail {
      display: block; 
    }
  }
}

@media (max-width: 800px) {
  .thumbnail {
    display: none;
  }
}
