.banner-container {
  display: flex;
  flex-direction: column;
  justify-content: left;
  color: aliceblue;
  background-color: rgb(0, 0, 0, 0.8);
  border-radius: 0.5rem;

  h1 {
    font-style: italic;
    margin: 2rem 4rem 0.5rem 4rem;
    text-align: left;
    max-width: 30rem;
    font-size: 40px;
  }

  p {
    margin-left: 4rem;
    font-size: large;
    text-align: left;
    max-width: 30rem;
    padding-right: 2rem;
  }

  h3 {
    margin: 0rem 4rem 2rem 4rem;
    text-align: left;
    width: 30rem;
  }
}
